import React, { useRef } from 'react'
import { FaPlus } from 'react-icons/fa'

function AddItem({newItem,setNewItem, handlesubmit}) {
    const inputRef = useRef();
  return (
<form className='addForm' onSubmit={handlesubmit}>
<label htmlFor='addItem'>Add Items</label>
<input autoFocus ref={inputRef} type='text' id='addItem' placeholder='Add Item' required value={newItem} onChange={(e)=>setNewItem(e.target.value)}/>
<button type='submit' aria-label='Add Item' onClick={()=>inputRef.current.focus()}><FaPlus/></button>
</form>
    )
}

export default AddItem